.menu {
  display: flex;
  justify-content: center;
}

.navbar-default {
  padding: 0;
  border-bottom: 15px solid #000;
  border-image: url('./assets/101.png');
  border-image-repeat: stretch;
  border-image-slice: 300;
  font-weight: 600;
  display: flex;
  justify-content: center;
  .navbar {
    display: flex;
    padding: 5px;
    margin: 5px;
    .nav-item {
      margin: 10px;
      padding: 10px;
      font-size: 15px;
      a {
        color: #a0a0a0;
      }
      a:hover {
        color: #170707;
        opacity: 1;
      }
    }
  }
}
